.movies-row {
    color: $clr1;
    margin-left: 20px;

    .row-posters {
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        padding: 20px;

        &::-webkit-scrollbar {
            display: none;
        }

        .row-poster {
            cursor: pointer;
            max-height: 100px;
            object-fit: contain;
            margin-right: 10px;
            width: 100%;
            transition: transform 450ms;

            &:hover {
                transform: scale(1.08);
                opacity: 1;
            }

            &.row-poster-large {
                max-height: 250px;

                &:hover {
                    transform: scale(1.09);
                    opacity: 1;
                }
            }
        }
    }
}