* {
    box-sizing: border-box;
}

body {
    font-size: 16px;
    margin: 0;
    font-family: NetflixSansRegular, Arial, Helvetica, sans-serif;
    letter-spacing: 2px;
    background-color: #141414;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    line-height: 1.3;
    margin: 0;
}

button {
    cursor: pointer;
    font-family: NetflixSansBold, Arial, Helvetica, sans-serif;
}