.main-banner {
    height: 450px;
    position: relative;
    object-fit: contain;
    color: $clr1;

    .banner-container {
        margin-left: 30px;
        padding-top: 150px;

        .banner-title {
            font-size: 3rem;
            padding-bottom: 0.3rem;
        }

        .banner-description {
            width: 25rem;
            line-height: 1.3;
            padding-top: 1rem;
            font-size: 0.8rem;
            height: 80px;
        }

        .banner-btns {
            button {
                border: none;
                color: $clr1;
                background-repeat: 10px;
                padding: 0.5rem 2rem;
                margin-right: 1rem;
                background-color: rgba(51, 51, 51, 0.58);
                
                &:hover{
                    color: $clr0;
                    background-color: $clr2;
                    transition: all 0.2s;
                }
            }

        }


    }

    .banner-fade-bottom {
        height: 7.5rem;
        background-image: linear-gradient(180deg,
                transparent,
                rgba(37, 37, 37, 0.61),
                #111);
    }
}