.app-header {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 60px;
    z-index: 1;

    // Animation
    transition-timing-function: ease-in;
    transition: all 0.5s;

    &.app-header-black {
        background-color: $clr0;
    }

    .header-container {
        display: flex;
        justify-content: space-between;

        .logo {
            position: fixed;
            top: 10px;
            left: 0;
            width: 80px;
            object-fit: contain;
            // padding-left: 20px;
            cursor: pointer;
        }

        .avatar {
            position: fixed;
            right: 20px;
            width: 30px;
            border-radius: 10px;
            cursor: pointer;
        }

    }
}